import React, { Component } from "react";
import ReactHTMLTableToExcel from "react-html-table-to-excel";
import { localStoragedecryp,formatGram, getUserdata } from "../../../../../components/helpers/function";
class LaporanHancurBarangExel extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  render() {
    let stock_on_hand = 0;
    let berat = 0;
    let berat_atribut = 0;
    let total_barang = 0;
    this.props.data.forEach((barang, index) => {
      barang.detail.forEach((item, index) => {
        stock_on_hand = stock_on_hand + item.stock_on_hand;
        berat_atribut = berat_atribut + item.berat_atribut;
        berat = berat + item.berat;
      });
      total_barang = total_barang + barang.detail.length;
    });
    return (
      <>
        <ReactHTMLTableToExcel
          id="test-table-xls-button"
          className="btn btn-success btn-block"
          table="table-to-xls"
          filename="Laporan Hancur Barang"
          sheet="Laporan Hancur Barang"
          buttonText="Export Exel"
        />
        <table
          rowkey="kode_member"
          id="table-to-xls"
          style={{ display: "none" }}
        >
          <thead>
            <tr>
              <th colSpan="8"> Laporan Hancur Barang </th>
            </tr>
            <tr>
              <th colSpan="8"> Periode </th>
            </tr>
            <tr>
              <th colSpan="8">
                {" "}
                {localStoragedecryp("tgl_laporan").length === 0
                  ? null
                  : JSON.parse(localStoragedecryp("tgl_laporan")).tgl_awal +
                    " s/d " +
                    JSON.parse(localStoragedecryp("tgl_laporan"))
                      .tgl_akhir}{" "}
              </th>
            </tr>
            <tr>
              <th colSpan="8"> TOKO :  {getUserdata().nama_toko} </th>
            </tr>
           
            <tr>
              <th colSpan="8"> ALAMAT :  {getUserdata().alamat_toko} </th>
            </tr>
            <tr>
              <th colSpan="8"></th>
            </tr>
            <tr>
            <th style={{ backgroundColor: "#E8E5E5", color: "#000" }}>
                NO
              </th>
              <th style={{ backgroundColor: "#E8E5E5", color: "#000" }}>
                KODE GUDANG
              </th>
              <th style={{ backgroundColor: "#E8E5E5", color: "#000" }}>
                KODE BAKI
              </th>
              <th style={{ backgroundColor: "#E8E5E5", color: "#000" }}>
                KODE BARCODE
              </th>
              <th style={{ backgroundColor: "#E8E5E5", color: "#000" }}>
                NAMA BARANG
              </th>
              <th style={{ backgroundColor: "#E8E5E5", color: "#000" }}>
                KONDISI BARANG
              </th>
              <th style={{ backgroundColor: "#E8E5E5", color: "#000" }}>
                NAMA ATRIBUT
              </th>
              <th style={{ backgroundColor: "#E8E5E5", color: "#000",textAlign : "right" }}>
                BERAT ATRIBUT
              </th>
              <th style={{ backgroundColor: "#E8E5E5", color: "#000",textAlign : "right" }}>
                BERAT
              </th>

              
            </tr>
          </thead>
          <tbody>
            {this.props.data.map((item, no) => (
              <div key={no}>
                {item.detail.map((row, index) => (
                  <tr key={index}>
                    <td> {++no}</td>
                    <td> {row.kode_gudang}</td>
                    <td> {row.kode_baki}</td>
                    <td>&nbsp;{row.kode_barcode}</td>
                    <td> {row.nama_barang}</td>
                    <td> {row.kondisi_barang}</td>
                    <td> {row.nama_atribut}</td>
                    <td style={{textAlign : "right"}}> {formatGram(row.berat_atribut,2)}</td>
                    <td style={{textAlign : "right"}}> {formatGram(row.berat,2)}</td>
                  </tr>
                ))}
                </div>
            ))}
          </tbody>
          <tfoot>
            <tr>
              <td
                colSpan="5"
                style={{ backgroundColor: "#E8E5E5", color: "#000",textAlign : "center" }}
              >
                {" "}
                Total
              </td>
            
              <td style={{ backgroundColor: "#E8E5E5", color: "#000" }}> </td>
              <td style={{ backgroundColor: "#E8E5E5", color: "#000" }}> </td>
              <td style={{ backgroundColor: "#E8E5E5", color: "#000", textAlign: "right" }}> {formatGram(berat_atribut,2)} </td>
              <td style={{ backgroundColor: "#E8E5E5", color: "#000",textAlign : "right" }}> {formatGram(berat,2)} </td>

              {/* <th
              colSpan="8"
              style={{ backgroundColor: "#E8E5E5", color: "#000",textAlign : "left" }}
            >
              Total Brang : {total_barang} Pcs<br />
              Total Berat : {berat.toFixed(3)} <br />
              Total Berat Atribut : {berat_atribut.toFixed(3) }
            </th> */}
            </tr>
          </tfoot>
        </table>
      </>
    );
  }
}
export default LaporanHancurBarangExel;
