import React from "react";
import { Link } from "react-router-dom";
import { Panel, PanelHeader } from "../../../../components/panel/panel.jsx";

import { connect } from "react-redux";
import { postDataNoTImeOut } from "../../../../config/axios.jsx";
import { reduxForm } from "redux-form";
import "react-datepicker/dist/react-datepicker.css";
import { NotifError, ToastNotification } from "../../../../components/helpers/notification.jsx";
import HeadHancurBarang from "./HeadHancurBarang.jsx";
import { formatDate, localStorageencryp } from "../../../../components/helpers/function.jsx";
import * as moment from "moment-timezone";


const maptostate = (state) => {
  return {
    hideModal: state.datamaster.modalDialog,
  };
};

class FormLaporanHancurBarang extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isEdit: false,
      modalDialog: false,
      isLoading: false,
      username: false,
      export: false,
      LaporanDataBarang: [],
      tgl_awal: new Date(),
      tgl_akhir: new Date(),
    };
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  handleSubmit(data) {
    this.setState({
      isLoading: true,
    });
    let data_post = {
      tgl_awal: formatDate(moment(new Date(data.tgl_awal)).format('YYYY-MM-DD')),
      tgl_akhir: formatDate(moment(new Date(data.tgl_akhir)).format('YYYY-MM-DD')),
    };

    postDataNoTImeOut("hancurbarang/get/report", data_post)
      .then((res) => {
        if (res.data.length === 0) {
          ToastNotification("info", "Data Yang Anda Cari Tidak Ada");
          this.setState({
            isLoading: false,
            export: false,
          });
        } else {
          localStorageencryp("tgl_laporan", JSON.stringify(data_post));
          ToastNotification("success", "Laporan Hancur Barang Tersedia");
          this.setState({
            LaporanDataBarang: res.data,
            isLoading: false,
            export: true,
          })
        }
      })
      .catch((error) => this.errorHandling(error));
  }

  errorHandling(err) {
    NotifError(err);
    this.setState({
      isLoading: false,
      export: false,
    });
  }
  render() {
    return (
      <div>
        <ol className="breadcrumb float-xl-right">
          <li className="breadcrumb-item">
            <Link to="#">Laporan Barang</Link>
          </li>
          <li className="breadcrumb-item active">Laporan Hancur Barang</li>
        </ol>
        <h1 className="page-header">Laporan Hancur Barang </h1>
        <Panel>
          <PanelHeader>Filter Laporan</PanelHeader>
          <br />
          <div className="container">
            <HeadHancurBarang
              export={this.state.export}
              LaporanDataBarang={this.state.LaporanDataBarang}
              isLoading={this.state.isLoading}
              onSubmit={(data) => this.handleSubmit(data)}
            />
          </div>
          <br/>
        </Panel>
      </div>
    );
  }
}

FormLaporanHancurBarang = reduxForm({
  form: "FormLaporanHancurBarang",
  enableReinitialize: true,
})(FormLaporanHancurBarang);
export default connect(maptostate, null)(FormLaporanHancurBarang);
